@import '../app_styles/global.scss';

// Login Page
.login-page{
    background-color: $White1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    & .disclaimer{
        width: 50%;
        color: $Purp1 !important;
    }

    & h1{
        opacity: 0;
        margin-bottom: 2.5%;
        color: $Purp1;
        animation: FadeIn 1000ms ease-in-out 50ms forwards; 
        &:last-of-type{
            margin-bottom: 5%;
        }

        // Delays

        &:nth-of-type(2){
            animation-delay: 150ms;
        }
        &:nth-of-type(3){
            animation-delay: 250ms;
        }
        &:nth-of-type(4){
            animation-delay: 350ms;
        }
    }

    & .login-link{
        opacity: 0;
        width: fit-content;
        padding-left: 0.5%;
        padding-right: 0.5%;
        border-bottom: 2px solid $Red1;
        font-weight: 1000;
        font-size: 1.5em;
        text-align: center;
        text-decoration: none;
        transition: all 150ms ease-in-out;
        animation: FadeIn 1250ms ease-in-out 50ms forwards; 
    }
    & a{
        text-decoration: none;
        color: $Purp1;
        &:visited{
            color: inherit;
        }
        &:hover{
            padding-left: 5%;
            padding-right: 5%
        }
    }
}
